
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.clearfloat:after {
  display: block;
  clear: both;
  content: "";
  visibility: hidden;
  height: 0;
}
.clearfloat {
  zoom: 1;
}
@-webkit-keyframes colorlunbo {
0% {
    color: #11189A;
}
20% {
    color: #643A68;
}
40% {
    color: #FFCD41;
}
60% {
    color: #A7C0DC;
}
80% {
    color: #41B983;
}
100% {
    color: #F18F93;
}
}
@keyframes colorlunbo {
0% {
    color: #11189A;
}
20% {
    color: #643A68;
}
40% {
    color: #FFCD41;
}
60% {
    color: #A7C0DC;
}
80% {
    color: #41B983;
}
100% {
    color: #F18F93;
}
}
/** iPad **/
@media only screen and (min-width: 751px) {
.nav-bar {
    width: 100%;
    height: 50px;
    background-color: #191818;
    position: fixed;
    top: 0;
    left: 0;
    margin-bottom: 50px;
}
.nav-bar .container {
    width: 80%;
    height: 100%;
    margin: 0 auto;
}
.nav-bar .container .container-left {
    width: 30%;
    height: 100%;
    float: left;
    /*border: 1px solid #000;*/
}
.nav-bar .container .container-left h3 {
    color: #eee;
    font-size: 15px;
    line-height: 50px;
}
.nav-bar .container .container-right {
    width: 70%;
    /*border: 1px solid #000;*/
    float: left;
}
.nav-bar .container .container-right ul {
    text-align: center;
}
.nav-bar .container .container-right ul li {
    float: left;
    padding: 0 5px;
    margin: 0 5px;
}
.nav-bar .container .container-right ul li span {
    color: #eee;
    font-size: 13px;
    line-height: 50px;
}
.nav-bar .container .container-right ul li:hover {
    background-color: #3F4042;
}
.con {
    width: 76%;
    margin: 0 auto;
}
.con .con-left {
    width: 66%;
    float: left;
}
.con .con-right {
    width: 33%;
    float: right;
}
.con .con-right .home_right_con {
    width: 100%;
    margin: 15px auto;
    text-align: left;
    border-radius: 8px;
    border: 1px solid #e3e3e3;
