    line-height: 35px;
    padding-left: 10px;
    color: #eeeeee;
    -webkit-animation: colorlunbo 20s infinite alternate;
    animation: colorlunbo 20s infinite alternate;
}
.con .con-right .home_right_con .home_right_con_body ul {
    margin-left: 10px;
}
.con .con-right .home_right_con .home_right_con_body ul li {
    margin: 10px 0;
    color: #eeeeee;
}
.con .con-right .home_right_con .home_right_con_body ul li span {
    margin: 0 3px;
    font-size: 13px;
}
.con .con-right .home_right_con .home_right_con_body ul li a {
    margin: 0 3px;
    font-size: 13px;
    color: #eeeeee;
}
.con .con-right .home_right_con .home_right_con_body ul li a:hover {
    color: #4592EC;
    cursor: pointer;
}
footer div {
    height: 30px;
    background-color: #191818;
    text-align: center;
}
footer div h3 {
    color: #eeeeee;
    font-size: 13px;
    line-height: 30px;
}
}
